body {
   font-family: sans-serif;
}

*,
*::before,
*::after {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

.container {
   margin: 0 auto;
   width: 100%;
   max-width: 900px;
}

#app {
   padding: 50px;
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   overflow-y: scroll;
}

.gallery {
   width: 100%;
   &-header {
      margin-bottom: 50px;
      display: flex;
      justify-content: space-around;
      background-color: #eee;

      &__tab {
         padding: 10px;
         font-size: 1.2rem;
         flex-grow: 1;
         text-align: center;
         transition: background-color .2s ease;
         cursor: pointer;
         &:hover {
            background-color: #e0dede7d;
         }
         &.active {
            background-color: rgb(224, 222, 222);
         }
      }
   }

   &-body {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      &__img {
         margin: 2px;
         overflow: hidden;
         flex-basis: 49%;
         max-height: 300px;
         img {
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
         }
      }
   }

}

